import React, { Component } from 'react'
import QuestionItem from '@/components/QuestionItem'
import './index.less'

import cvteLogo from '@/assets/cutImg/cvte logo.png'

export default class Questions extends Component {
  render() {
    return (
      <div className="Questions">
        {/* 头部 */}
        <div className="Questions-header">
          <div className="Questions-header-title">Questions</div>
          <div className="Questions-header-subTitle">
            <span>Record</span>
            <span className="daily-color"> daily </span>
            <span>&amp;</span>
            <span className="weekly-color"> weekly </span>
            <span>problems</span>
          </div>
          <div className="Questions-header-line"></div>
        </div>
        {/* 实际内容 */}
        <div className="Questions-content">
          {/* 年份 */}
          <div className="Questions-content-year">2021</div>
          {/* 左侧 */}
          <div className="Questions-content-left">
            <div className="Questions-content-left-mouth">
              mouth 1
            </div>
            <div className="Questions-content-left-logo">
              <img src={cvteLogo} alt=""/>
            </div>
          </div>
          {/* 中间时间线 */}
          <div className="Questions-content-center-line"></div>
          {/* 右边问题记录 */}
          <div className="Questions-content-right">
            {/* 第二天 */}
            <div className="Questions-content-right-day">
              <div className="day">day2</div>
              <div className="day-container">
                <QuestionItem question="这是一个问题" answer="www.baidu.com" answerLink="https://www.baidu.com"/>
                <QuestionItem question="如何实现这个按钮样式" answer="http://localhost:3000/questions" answerLink="http://localhost:3000/questions"/>
              </div>
            </div>
            {/* 第一天 */}
            <div className="Questions-content-right-day">
              <div className="day">day1</div>
              <div className="day-container">
                <QuestionItem question="这是一个问题" answer="www.baidu.com" answerLink="https://www.baidu.com"/>
                <QuestionItem question="如何实现这个按钮样式" answer="http://localhost:3000/questions" answerLink="http://localhost:3000/questions"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
