import React, { Component } from 'react'
import './index.less'

export default class QuestionItem extends Component {
  state = {
    isChecked: false,
    isChanged: false
  }

  changeCheckState = () => {
    const {isChecked,isChanged} = this.state
    // const offsetWidth = this.item.offsetWidth
    // this.setState({offsetWidth})
    this.setState({isChecked: !isChecked})
    setTimeout(() => this.setState({isChanged: !isChanged}),0)
  }

  render() {
    const {isChecked,isChanged} = this.state
    const offsetWidth = this.item?.offsetWidth
    return (
      <div>
        <div className={`QuestionItem ${isChecked ? 'checked' : ''}`} ref={(c) => this.item = c} onClick={this.changeCheckState}>
          <div 
            className="QuestionItem-icon" 
            style={{transform: `translateX(${isChanged ? `calc(${offsetWidth}px - 100% - 1vw)`  : '0'})`}}>
            <i className={`iconfont ${isChecked ? 'icon-xiayi' : 'icon-question-full'}`}></i>
          </div>
          {/* <span style={{transform:`translateX(${isChanged ? `calc(-${offsetWidth}px + 100% + 1vw)` : '0'})`}}> */}
          <span style={isChecked ? { marginRight: 3.5+'vw'} : {marginLeft: 3.5+'vw'}}>
            {isChecked ? 
              <a onClick={(e) => e.stopPropagation()} href={this.props.answerLink} target="_blank">{this.props.answer}</a> : 
              <span>{this.props.question}</span>}
          </span>
       </div> 
      </div>
    )
  }
}
